.creationForm {
  display: flex;
  flex-direction: column;
  width: 300px;
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  gap: 15px;

  &__label {
    display: flex;
    justify-content: space-between;
  }

  &__field {
    border: none;
    border-bottom: 1px solid gray;
    height: 20px;
    width: 200px;

    &:hover::placeholder {
      transition: text-indent ease-in-out 0.8s;
      text-indent: 500px;
    }

    &:focus-visible {
      outline: none;
    }
  }

  &__button {
    align-self: center;
  }
}

.active {
  border-bottom: 1px solid red;
  box-shadow: 0 0 1px 0 red;

  &::placeholder {
    color: red;
  }
}
