.users {
  &__list {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    gap: 15px;
    list-style-type: none;

    &_item {
      font-family: sans-serif;
      background-color: rgba(210, 255, 255, 0.15);
      border: 1px solid black;
      border-radius: 5px;
      padding: 10px;
      transition: background-color ease-in 0.3s;

      &:hover {
        background-color: rgba(210, 255, 255, 0.3);
      }
    }
  }

  &__button-block {
    display: flex;
    gap: 15px;
  }

  &__info-button {
    color: black;
  }

  &__delete-button {
    background-color: rgb(250, 200, 200);
  }
}